body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
    box-sizing: border-box;
    font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;
    font-weight: 400;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    line-height: 1.15;
}

.Home, .Individual, .Company {
  margin-top: 4.6%;
}

.contactus {
  background-color: lightgray;
  padding: 15% 0;
}
.Header {
    color: white;
    background-color: rgba(32, 35, 42, 0.9);
    padding : 16px 0;
    position: fixed;
    width: 100%;
    top: 0;
    overflow: hidden;
}

.Header > nav {
    display: inline;
}

.Header > a {
    text-decoration: none;
    color: white;
}

.Header > a > img {
    width: 10%;
    height: auto;
    margin: 0 10%;
    text-align: left;
}
.NatedIntro {
    background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),url(/nated/static/media/landing-background.eeea3d18.jpg);
    background-size: cover;
    background-position: center;
    min-height: 50%;
    color: white;
    padding: 20% 0;
    text-align: center;
}
.row {
    max-width: 1140px;
    margin: 0 auto;
  }
  
.referenceworks {
    padding: 80px 0;
}
  
.row {
    display: -webkit-flex;
    display: flex;
      -webkit-flex-direction: row;
              flex-direction: row;
      -webkit-flex-wrap: nowrap;
              flex-wrap: nowrap;
      -webkit-justify-content: space-around;
              justify-content: space-around;
      -webkit-align-items: center;
              align-items: center;
      -webkit-align-content: stretch;
              align-content: stretch;
}
  
.box {
    padding: 1%;
}
  
.box img {
    width: 100%;
    height: auto;
    margin-bottom: 15px;
}
.BusinessActivity {
    border: 1px solid black;
    margin: 10px;
}
